@import url(https://fonts.googleapis.com/css?family=Raleway:300,400&display=swap);
.App {
  text-align: center;
  font-family: 'Raleway', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.btn {
  cursor: pointer;
  padding: 8px;
  background-color: transparent;
  width: 80%;
  max-width: 200px;
}

.button-link {
  text-decoration: none;
}

h1 {
  font-size: 30px;
}
@media (min-width: 768px) {
  h1 {
    font-size: 36px;
  }
}
h2 {
  font-size: 24px;
}
@media (min-width: 768px) {
  h2 {
    font-size: 30px;
  }
}
p {
  line-height: 22px;
}


.text {
  color: #4a4a4a;
}

* {
  box-sizing : border-box;
}


.my-container {
  padding: 0 15px;
  max-width: 1220px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .my-container {
    padding: 0 20px;
  }
}

.form-section {
  margin: 30px 0;
}

.text-field {
  position: relative;
}

@media (min-width: 768px) {
  .text-field {
    padding: 0 10%;
  }
}

.text-field label {
  position: absolute;
  bottom: 7px;
  color: rgba(111, 109, 109, 0.9);
  font-size: .8rem;
  pointer-events: none;
  z-index: 0;
} 

.text-field svg {
  margin-right: 8px;
}

.focussed label {
  z-index: -1;
}

.contact-label {
  display: block;
  font-size: .85rem;
  margin-bottom: 7px;
  margin-top: 30px;
  position: relative !important;
}

.form-section .field {
  width: 80%;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #5a524e;
  box-shadow: none !important;
  height: 25px;
  font-size: 1rem;
}

#contact {
  width: 80%;
}


.colwill-checkbox {
  z-index: 0;
  position: relative;
  display: inline-block;
  font-size: .8rem;
  line-height: 1.5;
  padding: 0 10px;
}

@media (min-width: 768px) {
  .colwill-checkbox {
    font-size: .9rem;
  }
}

/* Input */
.colwill-checkbox > input {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  z-index: -1;
  position: absolute;
  left: calc(50% - 21px);
  top: -10px;
  display: block;
  margin: 0;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: #332c29;
  box-shadow: none;
  outline: none;
  border: none;
  opacity: 0;
  -webkit-transform: scale(1);
          transform: scale(1);
  pointer-events: none;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.2s;
  transition: opacity 0.3s, -webkit-transform 0.2s;
  transition: opacity 0.3s, transform 0.2s;
  transition: opacity 0.3s, transform 0.2s, -webkit-transform 0.2s;
}

.colwill-checkbox > .checkbox {
  display: inline-block;
  width: 100%;
  cursor: pointer;
  padding-top: 25px;
}

/* Box */
.colwill-checkbox > .checkbox::before {
  content: "";
  display: inline-block;
  box-sizing: border-box;
  border: solid 2px; /* Safari */
  border-color: #332c29;
  border-radius: 2px;
  width: 18px;
  top: 0;
  left: calc(50% - 10px);
  position: absolute;
  height: 18px;
  vertical-align: top;
  -webkit-transition: border-color 0.2s, background-color 0.2s;
  transition: border-color 0.2s, background-color 0.2s;
}

/* Checkmark */
.colwill-checkbox > .checkbox::after {
  content: "";
  display: block;
  position: absolute;
  width: 10px;
  top: 0;
  height: 5px;
  left: calc(50% - 10px);
  position: absolute;
  border: solid 2px transparent;
  border-right: none;
  border-top: none;
  -webkit-transform: translate(3px, 4px) rotate(-45deg);
          transform: translate(3px, 4px) rotate(-45deg);
}

/* Checked, Indeterminate */
.colwill-checkbox > input:checked {
  background-color: #332c29;
}

.colwill-checkbox > input:checked + .checkbox::before {
  border-color: #332c29;
  background-color: #332c29;
}

.colwill-checkbox > input:checked + .checkbox::after {
  border-color: #fff;
}

/* Hover, Focus */
.colwill-checkbox:hover > input {
  opacity: 0.04;
}

.colwill-checkbox > input:focus {
  opacity: 0.12;
}

.colwill-checkbox:hover > input:focus {
  opacity: 0.16;
}

/* Active */
.colwill-checkbox > input:active {
  opacity: 1;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: opacity 0s, -webkit-transform 0s;
  transition: opacity 0s, -webkit-transform 0s;
  transition: transform 0s, opacity 0s;
  transition: transform 0s, opacity 0s, -webkit-transform 0s;
}

.colwill-checkbox > input:active + .checkbox::before {
  border-color: #332c29;
}

.colwill-checkbox > input:checked:active + .checkbox::before {
  border-color: transparent;
  background-color: #332c29;
}

/* Disabled */
.colwill-checkbox > input:disabled {
  opacity: 0;
}

.colwill-checkbox > input:disabled + .checkbox {
  color: #332c29;
  cursor: auto;
  cursor: initial;
}

.colwill-checkbox > input:disabled + .checkbox::before {
  border-color: currentColor;
}

.colwill-checkbox > input:checked:disabled + .checkbox::before {
  border-color: transparent;
  background-color: currentColor;
}


.checkbox-group {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.checkbox-wrapper {
  flex-basis: 25%;
}

@media (min-width: 768px) {
  .checkbox-wrapper {
    flex-basis: 20%;
  }
}

.radio-wrapper {
  flex-basis: 25%;
}

@media (min-width: 768px) {
  .radio-wrapper {
    flex-basis: 20%;
  }
}

form.log-form {
  margin-top: 30px;
}

.form-section-title {
  color: rgb(119, 119, 119);
  font-size: .85rem;
}

